.Checkout {
  padding: 0rem 0rem 5rem;
}

.MainImage {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid var(--bright-green);
  box-shadow: 0.2rem 0.3rem 2.5rem rgba(0,0,0,0.5);
  height: 6.5rem;
  margin: -20% auto 0;
  overflow: hidden;
  position: relative;
  text-align: center;
  top: -1rem;
  width: 6.5rem;
}

.MainImage .fa-camera {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}
.CartItem {
  /* background-color: rgba(0,0,0,0.03); */
  /* background-color: var(--medium-green-10); */
  /* border: var(--border); */
  /* border-radius: var(--border-radius); */
  display: flex;
  justify-content: space-between;
  width: 88%;
  margin: 0.5rem auto;
  padding: 1rem;
  position: relative;
}

.CartItem .MainImage {
  border: none;
  /* box-shadow: none; */
  height: 5.5rem;
  width: 5.5rem;
  margin: 0;
  position: static;
}

.CartItem .itemInfo {
  color: var(--bright-green);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 65%;
}

.itemInfo h4 {
  color: var(--black-30);
  /* font-style: italic; */
  letter-spacing: 1px;
}

.itemInfo p {
  color: var(--blackish);
  font-size: 1.2rem;
  margin: 0.5rem 0rem;
}

.CartItem .clear {
  align-items: center;
  color: var(--danger-35);
  /* color: var(--black-30); */
  display: flex;
  font-size: 1.4rem;
  position: absolute;
  right: 0.5rem;
  top: 1.2rem;
  opacity: 0.9;
}

.clear p {
  opacity: 0.9;
  margin-right: 0.25rem;
  padding-bottom: 0.1rem;
  font-size: 1.1rem;
}

.cart-container {
  background-color: var(--medium-green);
  background-color: var(--light-green-85);
  min-height: 80vh;
  padding-top: 3.5rem;
}

.cart-container .basket {
  color: var(--white);
  color: var(--dark-green);
  color: var(--blackish);
  font-size: 1.5rem;
  font-weight: 100;
  letter-spacing: 0.1rem;
  margin-left: 1.5rem;
  padding: 1rem 0 2rem;
}

.inner-container {
  background-color: var(--white);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin-top: -1rem;
  min-height: 75vh;
  padding-top: 2rem;
  position: relative;
}

.clear-cart {
  align-items: center;
  border: var(--border);
  border-radius: var(--border-radius);
  display: flex;
  font-size: 1.3rem;
  justify-content: center;
  margin: 1rem 1.5rem 1.5rem auto;
  padding: 0.25rem;
  width: 30%;
}

.clear-cart i {
  color: var(--black-30);
  font-size: 1.3rem;
}

.clear-cart p {
  color: var(--black-30);
  margin-right: 0.5rem;
}

.CartFooter {
  align-items: center;
  background-color: var(--white);
  bottom: 0;
  border: var(--border);
  display: flex;
  justify-content: space-between;
  padding: 1rem 0rem;
  position: fixed;
  width: 100%;
  left: 0;
}

.CartFooter .total-container {
  margin-left: 1.5rem;
}

.total-container h4 {
  color: var(--text-gray);
  font-size: 1rem;
}

.CartFooter .total {
  color: var(--medium-green);
  font-size: 1.3rem;
  font-weight: 100;
}

.CartFooter .placeholder-btn {
  background-color: var(--medium-green);
  border-radius: var(--border-radius);
  box-shadow: var(--mini-box-shadow);
  box-shadow: 5px 5px 30px 0px var(--medium-green);
  color: var(--white);
  font-size: 1.3rem;
  margin-right: 1.5rem;
  padding: 0.5rem 0.75rem;
}

.placeholder-btn.disabled {
  background-color: var(--light-gray);
  box-shadow: none;
  pointer-events: none;
}

.CartFooter .fa-credit-card {
  margin-left: 0.75rem;
}

.EmptyCart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 60vh;
}

.EmptyCart .basket-icon {
  align-items: center;
  background: url(data:image/png;base64,);
  border-radius: 50%;
  display: flex;
  height: 50rem;
  justify-content: center;
  left: -47%;
  margin: 0 auto;
  position: fixed;
  top: -60%;
  width: 50rem;
  z-index: -1;
}

.basket-icon div {
  /* background-color: rgba(0, 0, 0, 0.07); */
  background-color: rgba(75,183,77,0.07);
  border-radius: 50%;
  height: 100%;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.basket-icon img {
  bottom: -1rem;
  height: 12%;
  position: absolute;
  width: 12%;
}

.EmptyCart .empty-message {
  margin-top: 24rem;
  text-align: center;
}

.EmptyCart h3 {
  color: var(--blackish);
  font-size: 1.4rem;
}

.EmptyCart h4 {
  color: var(--text-gray);
  font-size: 1.17rem;
  font-weight: 300;
  margin: 1.5rem auto;
  width: 50%;
}

.to-consider h3 {
  color: var(--text-gray);
  font-weight: 300;
}

.EmptyCart .LinkButton {
  background-color: var(--bright-green);
  border-radius: var(--border-radius);
  box-shadow: var(--black-box-shadow);
  box-shadow: 7px 7px 50px 0px var(--bright-green);
  margin: 2rem auto;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  width: 62%;
}

.EmptyCart a {
  color: var(--white);
  letter-spacing: 3px;
}

.CheckoutForm {
    align-items: center;
    /* background: linear-gradient(var(--light-green), var(--dark-green)); */
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    box-shadow: 0 0 25px 5px var(--black-20);
    display: flex;
    justify-content: center;
    position: fixed;
    width: 100vw;
    height: 52vh;
    padding-bottom: 3rem;
    z-index: 1;
    -webkit-transform: skewY(-5deg);
            transform: skewY(-5deg);
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;

    background-color: var(--white);
}

.CheckoutForm .card {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    position: relative;
    width: 100%;
    -webkit-transform: skewY(5deg);
            transform: skewY(5deg);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.CheckoutForm .card p {
    color: var(--blackish);
    margin-top: 1.5rem;
    text-align: center;
}

.CheckoutForm .fa-times-circle {
    color: var(--white);
    font-size: 1.5rem;
    position: absolute;
    right: 0.5rem;
    top: 0rem;

    color: var(--dark-gray);
}

.CheckoutForm .stripe-field {
    border-bottom: 1px solid var(--white);
    margin: 0 auto;
    padding-bottom: 0.5rem;
    width: 85%;

    border-bottom: 2px solid var(--bright-green);
}

.stripe-field input[type="email"] {
    color: var(--text-gray);
    font-size: 1.2rem;
    width: 98%;
    height: 2rem;
}

.stripe-field input[type="email"]::-webkit-input-placeholder {
    color: #AAA;
}

.stripe-field input[type="email"]::-ms-input-placeholder {
    color: #AAA;
}

.stripe-field input[type="email"]::placeholder {
    color: #AAA;
}

.CheckoutForm .card-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 85%;
}

.card-info .stripe-field {
    flex-basis: 28%;
}

.CheckoutForm .ActionButton {
    align-items: center;
    background: -webkit-linear-gradient(280deg, var(--light-green), var(--dark-green));
    background: linear-gradient(170deg, var(--light-green), var(--dark-green));
    border-radius: 1.5rem;
    box-shadow: var(--box-shadow) var(--medium-green-85);
    color: var(--blackish);
    display: flex;
    height: 2.75rem;
    justify-content: center;
    margin: 0 auto;
    width: 80%;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    
    /* background-color: var(--white); */
    /* color: var(--white); */
}

.CheckoutForm .ActionButton.success {
    background: -webkit-linear-gradient(var(--bright-green), var(--bright-green));
    background: linear-gradient(var(--bright-green), var(--bright-green));
}

.CheckoutForm .ActionButton.failed {
    background: -webkit-linear-gradient(var(--danger), var(--danger));
    background: linear-gradient(var(--danger), var(--danger));
}
.ActionButton .fas {
  margin-right: 0.5rem;
}

