.cart-container {
  background-color: var(--medium-green);
  background-color: var(--light-green-85);
  min-height: 80vh;
  padding-top: 3.5rem;
}

.cart-container .basket {
  color: var(--white);
  color: var(--dark-green);
  color: var(--blackish);
  font-size: 1.5rem;
  font-weight: 100;
  letter-spacing: 0.1rem;
  margin-left: 1.5rem;
  padding: 1rem 0 2rem;
}

.inner-container {
  background-color: var(--white);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin-top: -1rem;
  min-height: 75vh;
  padding-top: 2rem;
  position: relative;
}

.clear-cart {
  align-items: center;
  border: var(--border);
  border-radius: var(--border-radius);
  display: flex;
  font-size: 1.3rem;
  justify-content: center;
  margin: 1rem 1.5rem 1.5rem auto;
  padding: 0.25rem;
  width: 30%;
}

.clear-cart i {
  color: var(--black-30);
  font-size: 1.3rem;
}

.clear-cart p {
  color: var(--black-30);
  margin-right: 0.5rem;
}
